input[type="checkbox"]:checked + .checkbox::before {
  background: #bf0901;
  border: 2px solid #b30900; }

input[type="checkbox"]:checked + .checkbox::after {
  opacity: 1; }

input[type="radio"].active + .checkbox::before {
  border: 2px solid #b30900; }

input[type="radio"].active + .checkbox::after {
  opacity: 1; }

input[type="checkbox"], input[type="radio"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: none; }
  input[type="checkbox"] + .checkbox, input[type="radio"] + .checkbox {
    display: inline-block;
    margin-top: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    position: static;
    margin: 0;
    cursor: pointer; }
    input[type="checkbox"] + .checkbox.radio::before, input[type="radio"] + .checkbox.radio::before {
      border-radius: 50%;
      background: none; }
    input[type="checkbox"] + .checkbox.radio::after, input[type="radio"] + .checkbox.radio::after {
      content: '';
      top: 4px;
      left: 4px;
      width: 13px;
      height: 13px;
      border-radius: 50%;
      background: #bf0901; }
    input[type="checkbox"] + .checkbox::before, input[type="radio"] + .checkbox::before {
      height: 21px;
      width: 21px;
      display: block;
      content: "";
      background: transparent;
      border: 2px solid #575757;
      position: absolute;
      top: 0;
      left: 0; }
    input[type="checkbox"] + .checkbox::after, input[type="radio"] + .checkbox::after {
      opacity: 0;
      /*transition: opacity 0.15s ease-in;*/
      display: block;
      font: normal normal normal 14px/1 FontAwesome;
      content: "";
      color: #fff;
      position: absolute;
      top: 3px;
      left: 3px; }

