﻿.top-buffer {
    margin-top: $grid-gutter-width;
}

.bottom-buffer {
    margin-bottom: $grid-gutter-width;
}

.input-buffer {
    margin-top: floor(($grid-gutter-width / 2));
}

.full-width {
    .col-full-width {
        padding:0;
    }
}

.pad-level-xs-1 {
    padding-left: floor(($grid-gutter-width / 2)) !important;
    padding-right: floor(($grid-gutter-width / 2)) !important;
}


.pad-level-xs-2 {
    padding-left: $grid-gutter-width !important;
    padding-right: $grid-gutter-width !important;
}

.pad-level-xs-left-1 {
    padding-left: floor(($grid-gutter-width / 2)) !important;
}

.pad-level-xs-right-1 {
    padding-right: floor(($grid-gutter-width / 2)) !important;
}


.pad-level-xs-left-2 {
    padding-left: $grid-gutter-width !important;
}

.pad-level-xs-right-2 {
    padding-right: $grid-gutter-width !important;
}


